.outer-box {
    max-width: 87%;
    width: fit-content;
    height: fit-content;
    background-color: rgba(7,84,113,0.9);
    border-radius: 18px;

    top: 110px;
    bottom: 0;
    left: 0;
    right: 0;

    margin: auto;
    position: fixed;
    padding-bottom: 20px;
    padding-right: 10px;
    padding-left: 10px;
}


@media (max-width: 1024px) and (orientation: landscape) {

  .outer-box {
    max-width: 87%;
    width: fit-content;
    height: fit-content;
    background-color: rgba(6, 84, 113,0.9);
    border-radius: 18px;

    top: 155px;
    margin: auto;
    position: absolute;
    padding-right: 10px;
    padding-left: 10px;
  }
}

